import Vue from 'vue'
import Router from 'vue-router'
import 'lib-flexible/flexible'
import Report_info from "../components/Report_info";
import baidu_css from "../components/baidu_css";
import search_info from "../components/search_info";


Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/search/:word',
      name: '聚合',
      component: Report_info
    },
    {
      path:'/',
      name:'百度',
      component:baidu_css
    },
    {
      path:'/search_info/:keyword',
      name: '搜索详情',
      component:search_info
    },


  ]
})



