<template>
  <!-- <div class="box">
    <div class="searchBox">
      
      <div align="center" class="image"> <img src="../../src/assets/search_logo.png" height="149" width="290" style=""/></div>
      
      <input type="text" v-model="keyword" placeholder="请输入关键词进行搜索" class="searchInput" style="padding-left: 10px;" @keyup.enter="response">
      <input type="button"  @click="response" value="搜索" class="searchButton">
    </div>
  </div> -->
  <div id="header">
    <div id="logo">
      <img src="../../src/assets/search_logo.png" class="logo">

    </div> 
  <div class="searchbar">
        <img src="http://chuantu.xyz/t6/703/1575968896x2890212053.png" class="mg">
        <input type="text" id="search" value="请输入关键词进行搜索" v-model="keyword"
        onfocus="if(value=='请输入关键词进行搜索')value=''" onblur="if(!value)value='请输入关键词进行搜索'" name="keyword" @keyup.enter="response">
        
    </div>
  </div>

</template>

<script>
    export default {
      data () {
        return {
          keyword : ''
        }
      },
      methods: {
        response() {
          let url = 'http://credit.lawppt.com:9972/search/' + this.keyword;
          // let url = 'http://localhost:8003/search/' + this.keyword;
          window.open(url, '_blank')
        },

      },

    }
</script>

<style scoped>
  .box{
    margin: 0 auto;
    padding-top: 80px;
    height: 50px;
    width: 100%;
  }
  .searchBox{
    margin: 0 auto;
    width: 600px;
    position: relative;
  }
  .searchInput{
    display: inline-block;
    width: 85%;
    height: 38px;
    border: 1px solid #cccccc;
    float: left;
    box-sizing: border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
  }
  .searchButton{
    display: inline-block;
    width: 15%;
    height: 40px;
    line-height: 40px;
    position: absolute;
    background-color: #00a0e9;
    font-size: 16px;
    cursor: pointer;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
    border: none;
    color: #fff;
  }
  
#header {
     /* display: flex; */
     align-items: center;           /*子元素居中*/
     justify-content: flex-end;       /*子元素靠右*/
    }
#logo{
    text-align: center;
}

img.logo {
    width: 350px;    
}
#othernav {
    text-align: center;
}
img.othernav {
    margin: 60px 25px 0px 25px;
    width: 50px;
}


#search { 
    width: 450px;
    height: 40px;
    border:none;    /*取消默认的边框以设置自定义边框*/
    outline:none;   /*取消浏览器默认的蓝光边框以设置自定义的输入框*/
    font-size: 16px;        
    color: rgb(112, 112, 112);
}
.searchbar {        /*目的是设置自定义边框，比如圆角与阴影*/
    border: rgb(218, 218, 218) solid 1px;
    border-radius: 2em;
    width: 552px;
    height: 42px;
    box-shadow: 0px 0px 5px rgb(212, 212, 212);
    margin: 0 auto;
}

.mcp {
    height: 35px;
}

.mg {
    height: 25px;
    margin-left: 15px;
}
#search, .mcp, .mg {
    vertical-align: middle;
}



</style>



