<template>
  <Card style="width:auto;margin: 0 auto">
    <p class="report_title">
      裁决文书
    </p>
    <ul>
      <li v-for="item in movieList" style="list-style: none; margin-top: 40px; padding: 10px">
        <div><a :href="'/search_info/' + item.judgement.id" target="_blank" class="title_css">{{ item.judgement.title }}</a></div>
        <div class="digest_css">{{item.judgement.caseNo}}</div>
          <div class="source_css">来源:{{item.judgement.court}}</div>
          <div class="ReportTime_css">发布时间:{{item.judgement.judgeDate}}</div>
          <div class="clearfloat"></div>
      </li>
    </ul>
    <Page :total=total_num  style="text-align: center;margin-top: 20px" @on-change="changePage"></Page>
  </Card>
</template>

<script>
  import axios from 'axios'
    export default {
      data () {
        return {
          movieList: [],
          total_num : 0,
        }
      },
      methods: {
        response() {
          let that = this;
          let searchword = that.$route.params.word;
          // const path = 'http://calendar.tanruo.com:9992/main/api/testSearch/';
          const path = 'http://47.101.186.106:9992/main/api/testSearch/';
          axios.get(path, {params: {search:searchword}}).then(function (response) {
            let result = response.data.data;
            that.movieList =  result.judgements;
            let num = parseInt(result.total/50);
            if (num > 10) {
              that.total_num = 100
            } else {
              that.total_num = num
            }

          })
        },
        changePage(index) {
          let that = this;
          // const path = 'http://calendar.tanruo.com:9992/main/api/testSearch/';
          const path = 'http://47.101.186.106:9992/main/api/testSearch/';
          if(document.documentElement.scrollTop>0){
            document.documentElement.scrollTop=0;
          }
          axios.get(path, {params:{page:index}}).then(function (response) {
              let msg = response.data.data;
              that.movieList =  msg.judgements;
            }
          )
        },



      },
      created() {
        this.response()
      },
    }
</script>

<style scoped>
  .title_css {
    font-family: PingFangSC-Medium;
    font-size: 24px;
    font-weight: bold;
    color: #333333;
    letter-spacing: 0;
}
  .digest_css{
    font-family: PingFangSC-Regular;
    font-size: 28px;
    color: #333333;
    letter-spacing: 0;
    line-height: 42px;
  }
  .source_css{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    letter-spacing: 0;
    float: left;
  }
  .ReportTime_css {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    letter-spacing: 0;
    float: right;
  }
  .clearfloat{
    clear:both
  }
  .report_title{
    font-family: PingFangSC-Medium;
    font-size: 36px;
    color: #FF3B50;
    text-align: center;
  }
</style>



