<template>
  <div class="layout">
    <Layout>
      <Header :style="{position: 'fixed', width: '100%'}">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
          </div>
        </Menu>
      </Header>
        <Content style="margin: 88px 20px 0;padding: 50px;font-size: 20px">  {{search_data}}
        </Content>
      <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer>
    </Layout>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
      data () {
        return {
          search_data : ''
        }
      },
      methods: {
        response() {
          let that = this;
          let searchword = that.$route.params.keyword;
          const path = 'http://47.101.186.106:9992/main/api/testDetail/';
          axios.get(path, {params: {docid:searchword}}).then(function (response) {
            let result = response.data.data;
            that.search_data = result.judgement.plaintext
          })
        },
      },
      created() {
        this.response()
      }
    }
</script>

<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }
  .layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
  .layout-footer-center{
    text-align: center;
  }
</style>



